#about{
 min-height: 100vh;
 padding: 3rem 0rem 0rem;

 @media screen and (max-width:800px) {
padding: 2.5rem 0rem 2rem;
 }
}


.About-Head{
 font-size: 40px;
 text-align: center;
 margin-bottom: 50px;
 text-decoration: underline;
 @media screen and (max-width:1100px) {
 font-size: 30px;
 }
}


.fahad_about{
 display: flex;
@media screen and (max-width:1100px) {
 display: flex;
 flex-direction: column;
}
}

.image{
 width: 40%;
 margin-left: 75px;
 @media screen and (max-width:1100px) {
  width: 100%;
  margin-left: 0;
 }
}
.image>img{
 width: 330px;
 height: 330px;
 border-radius: 25px;
 display: block;
 margin: auto;
 @media screen and (max-width:1100px) {
 width: 270px;
 height: 270px;
 }
}

.text{
 width: 50%;
 padding-top: 20px;
 @media screen and (max-width:1100px) {
  width: 100%;
 }
}
.text>h1{
 @media screen and (max-width:1100px) {
 text-align: center;

 }
}
.text>p{
width: 90%;
font-size: 17px;
margin-top: 20px;
@media screen and (max-width:1100px) {
 text-align: center;
 margin: auto;
 margin-top: 20px;
}
}