#home{
 position: relative;
 .app__wrapper{
  padding: 0;
 }
}

.fahad_header{
 flex:1;
 width: 100%;
 display: flex;
 justify-content: center;
 align-items: center;
 padding: 5rem 3rem 0rem;
 min-height: 100vh;
 @media screen and (min-width: 2000px) {
  padding-top: 6rem;
 }
 @media screen and (max-width: 1200px) {
  flex-direction: column;
  padding: 6rem 0rem 3rem;
 }
 @media screen and (max-width: 480px) {
  padding: 4rem 0rem 0rem;
 }
}

.fahad_header_intro{
 flex: 1;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;

 @media screen and (max-width:2000px) {
  width: 100%;
  margin-right: 0rem;
 }

 @media screen and (max-width: 1200px) {
  flex: 0;
  margin-bottom: 30px;
 }

 @media screen and (max-width: 420px) {
  flex: 0;
  margin-bottom: 50px;
 }
}

.fahad_header_box{
 width: 90%;
 display: flex;
 justify-content: flex-end;
 align-items: center;
 flex-direction: column;

 
 .badge-cmp{
  padding: 1rem 2rem;
  border: var(--white-color);
  border-radius: 15px;
  background-color: white;
  width: auto;
  max-width: 500px;
  box-shadow: 0 0 20px rgba(0,0,0,0.1);
  

  @media screen and (max-width: 1200px) {
    text-align: center;
    padding: 1.5rem 2rem;
    width: 70%;
   }

  @media screen and (max-width:800px) {
    padding: 0.6rem 0.8rem;
    width: auto;
   }
   @media screen and (max-width:330px) {
    padding: 0.5rem 0.5rem;
    width: auto;
   }
 }

 .badge-cmp{

  .hello-hi{
  text-align: left;
  @media screen and (max-width: 1200px) {
  text-align: center;
  }
  @media screen and (max-width:650px) {
    font-size: 32px;
  }
  @media screen and (max-width:450px) {
    font-size: 28px;
  }
  }
  .new{
  margin-top: 4px;
  line-height: 1.5;
   @media screen and (max-width:650px) {
    font-size: 16px;
    margin-top: 2px;
    line-height: 1.2;
   }
   @media screen and (max-width:450px) {
    font-size: 14px;
    margin-top: 2px;
    line-height: 1.2;
   }
  }
 
 }

 .fahad_header_button{
  margin-top: 2rem;
  padding: 15px 20px;
font-weight: 500;
font-size: 18px;
background-color:black;
color: rgb(248, 247, 247);
border: none;
border-radius: 5px;
cursor: pointer;
transition: all 0.3s ease;

&:hover{
 background-color: white;
 color: black;
 
 font-weight: 600;
 border: 2px solid black;
}

@media screen and (max-width:1200px) {
  font-size: 17px;
  padding: 12px 16px;
 margin-top: 2rem;
}
@media screen and (max-width:650px) {
  font-size: 16px;
  padding: 10px 14px;
 margin-top: 1.8rem;
}
 }

 span{
  font-size: 2.5rem;

  @media screen and (min-width:2000px) {
   font-size: 5rem;
  }
 }

 @media screen and (max-width:1200px) {
  justify-content: center;
  align-items: center;
 }

 @media screen and (max-width:470px) {
width: 90%;
 }
}


.fahad_header_greet{
 flex:1;
 display: flex;
 justify-content: center;
 align-items: center;

 
 img{
   width: 90%;
   height: 90%;
   border-radius: 15px;
  }
 
  @media screen and (max-width: 1200px) {
    flex: 0;
   img{
    width: 85%;
    height: 85%;
  
   }
   }

   @media screen and (max-width: 470px) {
    flex: 0;
   img{
    width: 90%;
    height: 90%;
  
   }
   }

}



























